/* Home.css */

.home-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 50px;
  font-family: Arial, sans-serif;
  background-color: #f4f4f4;
}

.header {
  font-size: 36px;
  font-weight: bold;
}

.description {
  font-size: 18px;
  margin-bottom: 30px;
}

.features-section,
.get-started-section {
  width: 60%;
  margin-top: 40px;
  padding: 20px;
  border-radius: 8px;
  background-color: white;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.features-list {
  list-style-type: square;
  margin-top: 20px;
}

.feature-item {
  font-size: 16px;
  margin: 8px 0;
}

.get-started-section h2,
.features-section h2 {
  font-size: 24px;
}

.get-started-links {
  margin-top: 20px;
}

.get-started-links a {
  font-size: 18px;
  margin: 0 10px;
  text-decoration: none;
  color: blue;
}
